export const addu32 = (a, b) => ((a >>> 0) + (b >>> 0)) >>> 0;
export const divu32 = (a, b) => ((a >>> 0) / (b >>> 0)) >>> 0;
export const mulu32 = (a, b) => ((a >>> 0) * (b >>> 0)) >>> 0;
export const subu32 = (a, b) => ((a >>> 0) - (b >>> 0)) >>> 0;
